<template>
    <div class="mt-10 grid items-center justify-center">
        <div class="grid lg:grid-cols-4 md:grid-cols-3 sm:grid-cols-2 grid-cols-1 gap-x-8 gap-y-12">
            <Gif @show-modal="getGifObj" v-for="gif in gifs" :key="gif.id" v-bind:gif="gif"/>
        </div>

        <ViewModal v-bind:showing="ShowViewModal" @close-modal="ShowViewModal = false">
            <div class="w-full">
                <img class="h-96 w-full bg-no-repeat bg-cover rounded-xl" :src="GifObj.gifUrl" alt="Can't load GIF">
                <div class="py-3 flex items-start justify-between">
                    <div class="flex">
                        <img class="w-14 h-14 mr-2 rounded-full" :src="GifObj.userAvatarUrl" alt="avatar">
                        <div class="flex flex-col">
                            <p :title="GifObj.userName" class="md:text-2xl text-base font-black md:w-96 w-60 truncate">{{ GifObj.userName }}</p>  
                            <p class="">Rating: <span class="uppercase font-bold">{{ GifObj.rating }}</span></p>  
                        </div>
                    </div>
                    <div class="flex pr-2">
                        <a :href="GifObj.profile_url" target="_blank" rel="noopener noreferrer" class="hover:text-blue-500">
                            <svg xmlns="http://www.w3.org/2000/svg" class="mx-1" width="24" height="24" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round">
                                <path stroke="none" d="M0 0h24v24H0z" fill="none"></path>
                                <circle cx="12" cy="7" r="4"></circle>
                                <path d="M6 21v-2a4 4 0 0 1 4 -4h4a4 4 0 0 1 4 4v2"></path>
                            </svg>
                        </a>
                        <a :href="GifObj.gifUrl" target="_blank" rel="noopener noreferrer" class="hover:text-blue-500">
                            <svg xmlns="http://www.w3.org/2000/svg" class="mx-1" width="22" height="22" viewBox="0 0 22 22" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round">
                                <path stroke="none" d="M0 0h24v24H0z" fill="none"></path>
                                <path d="M11 7h-5a2 2 0 0 0 -2 2v9a2 2 0 0 0 2 2h9a2 2 0 0 0 2 -2v-5"></path>
                                <line x1="10" y1="14" x2="20" y2="4"></line>
                                <polyline points="15 4 20 4 20 9"></polyline>
                            </svg>
                        </a>
                    </div>
                </div>
            </div>
        </ViewModal>
    </div>
</template>

<script>
import Gif from './Gif.vue'
import ViewModal from './ViewModal.vue'

export default {
  name: 'GifList',

  props: {
      gifs: Array,
  },

  components: {
    Gif,
    ViewModal,
  },

  data() {
    return {
        ShowViewModal: false,
        GifObj: {
            gifUrl: '',
            userName: '',
            userAvatarUrl: '',
            profile_url: '',
            rating: '',
        }
    }
  },

  methods: {
    getGifObj(gifObj){
        this.GifObj.gifUrl = gifObj.images.original.url;
        this.GifObj.userName = gifObj.user.display_name;
        this.GifObj.userAvatarUrl = gifObj.user.avatar_url;
        this.GifObj.profile_url = gifObj.user.profile_url;
        this.GifObj.rating = gifObj.rating;
        this.ShowViewModal = true;
    }
  },
}
</script>
