<template>
    <section>
      <Nav @open-modal="ShowViewModal = true"/>
      <main>
        <div class="max-w-7xl mx-auto py-6 sm:px-6 lg:px-8">
          <div class="px-4 py-6 sm:px-0">
            <Search @fetch-gifs="onFetch"/>
            <div v-if="gifs.length == 0" class="col-start-2 col-end-4">
              <p class="text-center font-bold text-gray-500 text-xl">Loading...</p>
            </div>
            <GifList v-bind:gifs="gifs"/>
          </div>
        </div>
      </main>
      <ViewModal v-bind:showing="ShowViewModal" @close-modal="ShowViewModal = false">
            <div class="w-full">
                <div class="pt-3 h-80">
                  <h1 class="font-black text-lg py-3">About</h1>
                  <div class="leading-5 sm:leading-8">This simple application is using <a href="https://developers.giphy.com/docs/api#quick-start-guide" target="_blank" rel="noopener noreferrer" class="hover:text-blue-400">
                    <b>GIPHY API</b></a> by Giphy. It is neither endorsed nor sponsored by Giphy, it was built for learning purposes and it includes exploring APIs. The access of the application to the said API
                    can be limited, suspended or terminated at any time. All GIFs are given proper attribute or source (GIPHY User).
                  </div>
                  <h5 class="font-semibold pt-6">- Jade</h5>
                  <h5 class="py-2">Email: jadelestersoriano@gmail.com ðŸ˜Š</h5>
                </div>
            </div>
      </ViewModal>
      <footer>
        <div class="flex items-center justify-center mt-24 py-2 w-full bg-blue-200">
          <p class="font-bold px-1">Powered by Giphy</p>
          <img class="w-24" alt="GIPHY logo" src="./assets/img/GIPHY.png">
        </div>
      </footer>
    </section>
</template>

<script>
import Nav from './components/Nav.vue'
import Search from './components/Search.vue'
import GifList from './components/GifList.vue'
import ViewModal from './components/ViewModal.vue'

export default {
  name: 'App',

  components: {
    Nav,
    Search,
    GifList,
    ViewModal
  },

  data() {
    return {
        gifs: [],
        ShowViewModal: false,
    }
  },

  methods: {
    onFetch(result) {
      var data = result.data;
      for (var i = data.length - 1; i > -1; i--) {
          if (!Object.prototype.hasOwnProperty.call(data[i], 'user')) {
              data.splice(i, 1);
          }
      }
      this.gifs = data;
    }
  },
}
</script>

