<template>
    <a role="button" class="focus:outline-none btn px-3 text-blue-400" @click="onClick(tag.name)">{{ tag.name }}</a>
</template>
<script>
export default {
    props: {
        tag: Object,
    },

    methods: {
        onClick(tag) {
            this.$emit('fetch-tag', tag);
        }
    }
}
</script>