<template>
    <div>
        <img @click="view(gif)" class="gif cursor-pointer bg-no-repeat bg-cover col-span-1 rounded-xl" :src="gif.images.fixed_height_still.url" alt="GIF">
        <div class="py-2 flex">
            <img class="w-6 h-6 bg-no-repeat bg-cover mr-2 rounded-full" :src="gif.user.avatar_url" alt="GIF">
            <a :href="gif.user.profile_url" target="_blank" rel="noopener noreferrer" class="text-left no-underline hover:text-gray-500 w-52 truncate" :title="gif.user.display_name">
                {{ gif.user.display_name }}
            </a>
        </div>
    </div>
</template>

<script>
export default {
  name: 'Gif',

  props: {
      gif: Object
  },

  methods: {
    view(gifObj) {
        this.$emit('show-modal', gifObj);
    }
  }
}
</script>

<style scoped>
    .gif {
        width: 240px;
        height: 200px;
    }
    .sgif {
        width: 100px;
        height: 100px;
    }
</style>