<template>
  <div class="px-4">
        <div class="h-16 md:w-2/4 w-full mx-auto relative">
            <div class="inline-block absolute left-5 top-6 text-gray-400">
                <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="search" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" class="w-4">
                    <path fill="currentColor" d="M505 442.7L405.3 343c-4.5-4.5-10.6-7-17-7H372c27.6-35.3 44-79.7 44-128C416 93.1 322.9 0 208 0S0 93.1 0 208s93.1 208 208 208c48.3 0 92.7-16.4 128-44v16.3c0 6.4 2.5 12.5 7 17l99.7 99.7c9.4 9.4 24.6 9.4 33.9 0l28.3-28.3c9.4-9.4 9.4-24.6.1-34zM208 336c-70.7 0-128-57.2-128-128 0-70.7 57.2-128 128-128 70.7 0 128 57.2 128 128 0 70.7-57.2 128-128 128z"></path>
                </svg>
            </div>
            <div class="inline-block absolute right-5 top-6 text-gray-600">
                <svg class="cursor-pointer" @click="clearKeyword" v-show="keyword != ''" viewBox="0 0 24 24" width="20" height="20" stroke="currentColor" stroke-width="2" fill="none" stroke-linecap="round" stroke-linejoin="round"><line x1="18" y1="6" x2="6" y2="18"></line><line x1="6" y1="6" x2="18" y2="18"></line></svg>
            </div>
            <input type="text" placeholder="Search awesome GIFs!" v-model="keyword" @input="onInput" class="w-full h-full rounded-lg shadow-md px-14 focus:outline-none">
        </div>

        <div class="my-12 flex flex-col items-center justify-center">
            <p class="text-gray-600 text-base">Find the best, funny and newest animated Gifs, Stickers and more!</p>
            <div class="mt-3 flex sm:flex-row flex-col items-center justify-center">
                <p class="px-2 font-bold sm:py-0 py-2">Powered by</p>
                <a href="https://giphy.com/" target="_blank" rel="noopener noreferrer">
                    <img class="w-24" alt="GIPHY logo" src="../assets/img/GIPHY.png">
                </a>
            </div>
        </div>

        <div v-show="tags != Array" class="flex items-start mb-4">
            <h1 class="font-bold pr-2" >Related Tags:</h1>
            <div class="flex overflow-x-auto">
                <Tag @fetch-tag="onClickTag" v-for="tag in tags" :key="tag.id" v-bind:tag="tag"/>
            </div>
        </div>

        <div v-show="isSuccess" class="w-full flex items-center justify-between mt-2">
            <a role="button" tabindex="0" :disabled="disableBtn" @click="prev" class="cursor-pointer relative inline-flex items-center p-3 rounded-full border border-gray-300 bg-white text-sm font-medium text-gray-500 active:bg-red-500">
                <span class="sr-only">Previous</span>
                <svg class="h-5 w-5" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true">
                    <path fill-rule="evenodd" d="M12.707 5.293a1 1 0 010 1.414L9.414 10l3.293 3.293a1 1 0 01-1.414 1.414l-4-4a1 1 0 010-1.414l4-4a1 1 0 011.414 0z" clip-rule="evenodd" />
                </svg>
            </a>
            <a role="button" tabindex="1" :disabled="disableBtn" @click="next" class="cursor-pointer relative inline-flex items-center p-3 rounded-full border border-gray-300 bg-white text-sm font-medium text-gray-500 active:bg-red-500">
                <span class="sr-only">Next</span>
                <svg class="h-5 w-5" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true">
                    <path fill-rule="evenodd" d="M7.293 14.707a1 1 0 010-1.414L10.586 10 7.293 6.707a1 1 0 011.414-1.414l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0z" clip-rule="evenodd" />
                </svg>
            </a>
        </div>
    </div>
</template>

<script>
import Tag from './Tag.vue';

export default {
    name: 'Search',

    components: {
        Tag
    },

    created() {
        this.onLoadFetchGifs()
    },

    data(){
        return {
            keyword: '',
            timeout: null,
            paginate: 0,
            max: 20,
            tags: Array,
            url: {
                base: 'https://api.giphy.com/v1',
                trending: '/gifs/trending',
                search: '/gifs/search',
                relatedTag: '/tags/related/',
                key: '?api_key=27e764dB74OpftShpvqe1p1HnKrdvgrO',
                rating: '&rating=pg',
                limit: `&limit=${ this.max }`,
            },
            isSuccess: false,
            disableBtn: false,
        }
    },

    methods: {
        clearKeyword(){
            this.keyword = '';
            this.onLoadFetchGifs();
        },

        onInput(){
            clearTimeout(this.timeout);
            this.timeout = setTimeout(() => {
                this.search();
            }, 500);
        },

        failedFetch(){
            setTimeout(() => {
                if (!alert('Unable to load data. Please reload the page.')){ 
                    window.location.reload(); 
                }
            }, 3000);
        },

        onLoadFetchGifs(){
            this.axios.get(this.url.base 
                + this.url.trending 
                + this.url.key 
                + this.url.rating 
                + this.url.limit 
                + `&offset=${ this.paginate }`)
            .then( response => {
                this.$emit('fetch-gifs', response.data);
            })
            .then( () => {
                this.isSuccess = true;
            })
            .catch( error => {
                this.isSuccess = false;
                console.log(error);
                this.failedFetch();
            });
        },

        fetchTags() {
            this.axios.get(this.url.base
                + this.url.relatedTag
                + `&q=${ this.keyword }`
                + this.url.key)
            .then( response => {
                this.tags = response.data.data;
            });
            console.log(this.url.searchKeyword);
        },

        search() {
            if(this.keyword !== ''){
                this.fetchTags();
                this.axios.get(this.url.base
                    + this.url.search
                    + this.url.key
                    + this.url.rating
                    + this.url.limit
                    + `&q=${ this.keyword }`
                    + `&offset=${ this.paginate }`)
                .then( response => {
                    this.$emit('fetch-gifs', response.data);
                })
                .then( () => {
                    this.isSuccess = true;
                })
                .catch( error => {
                    this.isSuccess = false;
                    console.log(error);
                    this.failedFetch();
                });
            } 
            else {
                this.onLoadFetchGifs();
            }
        },

        onClickTag(value) {
            this.keyword = value;
            this.search();
        },

        paginateResult() {
            if (this.keyword === '' && this.tags.length == 0) {
                this.onLoadResult();
            } else {
                this.search();
            }
        },

        temporaryDisableBtn() {
            this.disableBtn = true;
            setTimeout(() => {
                this.disableBtn = false;
            }, 3000);
        },

        next() {
            this.paginate+=this.max;
            this.paginateResult();
            this.temporaryDisableBtn();
            console.log('hap');
        },

        prev() {
            if (this.paginate === 0){
                return;
            }

            this.paginate-=this.max;
            this.paginateResult();
            this.temporaryDisableBtn();
            console.log('sad');
        },
    },
}
</script>
